/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
vdl-tertiary-nav vdl-list vdl-list-item:not(:last-child) {
  border-bottom: 1px solid #f9f9f9; }
  vdl-tertiary-nav vdl-list vdl-list-item:not(:last-child):hover {
    border-bottom: 1px solid #f3f3f3;
    color: #3a7da9; }

vdl-tertiary-nav vdl-list vdl-list-item.selected .vdl-list-item {
  background-color: #f3f3f3;
  font-family: Roboto, sans-serif;
  color: #3a7da9;
  border-left: 3px solid transparent; }
  [dir='rtl'] vdl-tertiary-nav vdl-list vdl-list-item.selected .vdl-list-item {
    border-right: 3px solid transparent;
    border-left: none; }

vdl-tertiary-nav vdl-list vdl-list-item .vdl-list-item {
  font-family: Roboto Light, sans-serif;
  font-size: 14px;
  line-height: 16px;
  height: 40px; }

vdl-tertiary-nav vdl-list vdl-list-item .vdl-list-item.vdl-list-item-select {
  padding-left: 17px;
  padding-right: 20px; }

vdl-tertiary-nav vdl-list vdl-list-item .vdl-list-item.vdl-list-item-focus.vdl-list-item-select {
  background-color: #f3f3f3;
  font-family: Roboto, sans-serif;
  color: #3a7da9;
  border-left: 3px solid transparent; }
  [dir='rtl'] vdl-tertiary-nav vdl-list vdl-list-item .vdl-list-item.vdl-list-item-focus.vdl-list-item-select {
    border-right: 3px solid transparent;
    border-left: none; }
  vdl-tertiary-nav vdl-list vdl-list-item .vdl-list-item.vdl-list-item-focus.vdl-list-item-select.vdl-list-item-select:hover {
    background-color: #f3f3f3; }
